import { Component } from '@angular/core';
import { NavController,NavParams, AlertController } from 'ionic-angular';
import { TabsPage } from '../tabs/tabs';
import { newsService } from '../../providers/newsService';
import { ToastService } from '../../providers/toastService';
import { ImgService } from '../../providers/imgService';
//mport { missionService } from '../../providers/missionService';

@Component({
  templateUrl: 'addNews.html',
  styles: ['./addNews.scss']
})

export class AddNewsPage {
  
  userInfo;
  constructor(public navCtrl: NavController,
            public navParams: NavParams,
            public alertCtrl: AlertController,
            public newsService: newsService,
            public toastSer: ToastService,
            public imgSer: ImgService,
             ) {
         this.userInfo=navParams.data.userInfo;
  }

  showAlert1() {
    const alert = this.alertCtrl.create({
      title: '错误提示',
      subTitle: '请填写分享内容',
      buttons: ['确认']
    });
    alert.present();
  }

  showAlert2() {
    const alert = this.alertCtrl.create({
      title: '错误提示',
      subTitle: '分享失败',
      buttons: ['重试']
    });
    alert.present();
  }

  avatarChoice() {
    this.initImgSer();
    this.imgSer.showPicActionSheet();
  }

  initImgSer() {
    this.imgSer.upload.url = 'assets/imgs'; // 上传图片的url，如果同默认配置的url一致，那无须再设置
    this.imgSer.upload.success = (data) => {
      //上传成功后的回调处理
      //this.doctorData.avatar = data.data.url;
    };
    this.imgSer.upload.error = (err) => {
      this.toastSer.showToast('错误：上传失败！');
    };
  }

  addNews(imageURL, content){

    if(!content)
    {  
      this.showAlert1();
      return;
    }

    var message = {
        "content":content,
        "imageURL":imageURL,
        "author":this.userInfo
    }

    var responseMessage=this.newsService.addNews(JSON.stringify(message));
    responseMessage.subscribe( response => {
        if(response["_body"]=="true"){
          this.navCtrl.push(TabsPage, {userInfo: this.userInfo});
        }
        else{
          this.showAlert2();
        }
     });
    
  }

}



